import React from 'react';
import { BillSubscriptionDescr, BillSubscriptionName } from '../../../components';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';
import clsx from 'clsx';

type Props = {
	name: string;
	description: string;
	children: React.ReactNode;
};
export function SelectedSubscription({ name, description, children }: Props) {
	const { isRTL } = useTranslationHelpers();
	return (
		<div
			className={clsx('mt-3 rounded-lg border border-kgrey-light-grey-6 p-5 ', {
				'lg:mr-12': !isRTL,
				'lg:ml-12': isRTL
			})}
		>
			<BillSubscriptionName name={name} />
			<BillSubscriptionDescr description={description} />
			{/*<div className="bill-subscr__descr text-xs text-kgrey-dark-4 mb-4">{description}</div>*/}
			<div className="bill-subscr__features">{children}</div>
		</div>
	);
}
