import React from 'react';
import { NewButton, PaymentFeature } from 'shared/components/Elements';
import { SelectedSubscription } from './SelectedSubscription';
import { InfoRow } from './InfoRow';
import { useAuth } from '../../../../../modules/auth/hooks/useAuth';
import { ConfirmModal, Loader } from '../../../../../shared/components';
import dayjs from 'dayjs';
import { useSubscription } from '../../../hooks';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';
import clsx from 'clsx';

type Props = {
	type?: 'small' | 'pro' | 'enterprise' | 'custom';
};
export function Subscription({ type = 'small' }: Props) {
	const { t } = useTranslation();
	const { isRTL } = useTranslationHelpers();
	const { companies } = useAuth();
	const {
		navigateToCheckout,
		subscription,
		handleCancelRequest,
		setCancelSubscription,
		cancelSubscription,
		cancelLoading
	} = useSubscription();

	if (!companies.length) {
		return <Loader withOverlay />;
	}

	const toggleCancelPopup = () => setCancelSubscription((prev) => !prev);

	return (
		<div className="bill-subscr border-b border-kgrey-d8d8d8 pb-9">
			<h3 className="bill-subscr__heading mb-2.5 text-xl font-medium dark:text-white">
				{t('subscription')}
			</h3>
			{subscription?.subscription_type === 'PRO' ? (
				<>
					<div className="bill-subscr__col-wrapper flex flex-col lg:flex-row">
						<div className="bill-subscr__col w-full lg:w-[421px]">
							<h5 className="bill-subscr__col-heading uppercase text-kgrey-menuText ">
								{t('your_plan')}
							</h5>
							<SelectedSubscription name="" description="">
								<>
									<PaymentFeature
										text={t('paid_members_info', {
											paidStaffCount: subscription.paid_staffs_count
										})}
									/>
									<PaymentFeature text={t('phone_support')} />
								</>
							</SelectedSubscription>
						</div>
						<div className="billing-subscription__col w-full lg:w-[421px]">
							<h5 className="bill-subscr__col-heading mb-3 uppercase text-kgrey-menuText">
								{t('billing_details')}
							</h5>
							<div className="bill-subscr__col-wrapper flex-center-between mt-9">
								<div className="left w-[227px] flex-shrink-0">
									<InfoRow
										prop={t('billing_start_date')}
										value={dayjs(subscription.start_date).format('DD/MM/YYYY')}
										noBorder
										noTopPadding
										noBottomPadding
									/>
									<InfoRow
										prop={t('billing_end_date')}
										value={dayjs(subscription.end_date).format('DD/MM/YYYY')}
									/>
									<InfoRow
										prop={t('paid_users_count')}
										value={`${subscription?.paid_staffs_count}`}
									/>
									<InfoRow
										prop={t('total_amount')}
										value={`$ ${subscription.total_amount}`}
										noBorder
										noBottomPadding
										propClassName="text-kdark dark:text-white"
										valueClassName="!font-bold !font-base"
									/>
								</div>
								<div className={clsx('right w-full', { 'pl-20': !isRTL, 'pr-20': isRTL })}>
									<NewButton
										fullWidth
										className="mb-4 dark:bg-primary-dark dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300"
										onClick={navigateToCheckout}
									>
										{t('upgrade')}
									</NewButton>
									{!subscription.is_cancelled ? (
										<NewButton
											fullWidth
											color="secondary"
											className="dark:bg-kred-pink dark:text-white dark:hover:bg-kred-pink-200"
											onClick={toggleCancelPopup}
										>
											{t('cancel')}
										</NewButton>
									) : (
										<div className="text-xs dark:text-white">{t('subscription_canceled_info')}</div>
									)}

									{/*Cancel Subscription Popup*/}
									<ConfirmModal
										featureHandler={handleCancelRequest}
										isOpen={Boolean(cancelSubscription)}
										toggleHandler={toggleCancelPopup}
										title={t('cancel_subscription_confirmation')}
										hasLoader
										isLoading={cancelLoading}
									/>
								</div>
							</div>
						</div>
					</div>
				</>
			) : (
				<p className="text-md mt-10 mb-4 text-kgrey-menuText">{t('no_subscription')}</p>
			)}
		</div>
	);
}
