import React, { useEffect } from 'react';
import { usePaymentContext } from '../../context/PaymentContext';
import { frequencyMockData } from '../../mocks';
import { IOSSwitchDuo } from 'shared/components/Elements/Fields/SwitchCompDuo';
import { useSwitch } from 'shared/hooks';
import { useTranslation } from 'react-i18next';

type Props = {
	yearlyDiscount: number;
};

export function PaymentFrequency({ yearlyDiscount }: Props) {
	const { t } = useTranslation();
	const { setPaymentFrequency } = usePaymentContext();

	const { checked, handleChange } = useSwitch({ frequency: false });

	useEffect(() => {
		setPaymentFrequency(checked.frequency ? 'month' : 'year');
	}, [checked]);

	return (
		<div className="py-10 px-4">
			<div className="flex-center-y">
				<FrequencyLabel
					title={t(frequencyMockData[0].title)}
					description={t('plan_description_yearly', { yearlyDiscount })}
				/>
				<IOSSwitchDuo checked={checked.frequency} onChange={handleChange} name="frequency" />
				<FrequencyLabel
					title={t(frequencyMockData[1].title)}
					description={t(frequencyMockData[1].description)}
				/>
			</div>
		</div>
	);
}

type FrequencyLabelProps = {
	title: string;
	description: string;
};

function FrequencyLabel({ title, description }: FrequencyLabelProps) {
	return (
		<dl className="mr-3.5">
			<dd className="text-md font-semibold dark:text-white">{title}</dd>
			<dt className="text-10px text-primary-dark">{description}</dt>
		</dl>
	);
}
