import React from 'react';
import { RoundCheck } from 'shared/components/Elements';
import { usePaymentContext } from '../context/PaymentContext';
import { PlanType } from '../types';
import { parsePlans } from '../utils/parsePlans';
import { Trans, useTranslation } from 'react-i18next';

type Props = {
	planItems: PlanType[];
};

export function RegularTariffList({ planItems }: Props) {
	const { t } = useTranslation();
	const { paymentFrequency } = usePaymentContext();
	const parsedPlanItems = parsePlans(planItems);

	return (
		<div className="rounded-lg border-t-4 border-kblue-3F598A bg-kgrey-light-grey-2 py-10 px-7 dark:border-kblue dark:bg-kdark-dark333 ">
			<h3 className="mb-6 text-xl font-semibold text-kblue-3F598A dark:text-kblue">
				{t('monthly_discount_plan')}
			</h3>
			<>
				{parsedPlanItems.map((subscription) => {
					const perMemberPrice = subscription.price[paymentFrequency];
					return (
						<div key={subscription.id} className="flex-center-y mb-4 last:mb-0">
							<RoundCheck />
							<p className="ml-2.5 text-xs text-kdark-183B56 dark:text-kblue">
								<Trans i18nKey="price_per_user">
									<strong>${{ perMemberPrice }}</strong> per user :
								</Trans>
								<Trans i18nKey="plan_member_count">
									<strong>{{ users: subscription.members }}</strong> users
								</Trans>
							</p>
						</div>
					);
				})}
			</>
		</div>
	);
}
