import React from 'react';
import clsx from 'clsx';
type Props = {
	prop: string;
	value: string;
	propClassName?: string;
	valueClassName?: string;
	noTopPadding?: boolean;
	noBottomPadding?: boolean;
	noBorder?: boolean;
};
export function InfoRow({
	prop,
	value,
	propClassName = '',
	valueClassName = '',
	noTopPadding = false,
	noBottomPadding = false,
	noBorder = false
}: Props) {
	return (
		<div
			className={clsx('bill-subscr__next-row  flex-center-between w-full text-sm', {
				'pt-2': !noTopPadding,
				'pb-2': !noBottomPadding,
				'border-b border-kgrey-light-grey-6': !noBorder
			})}
		>
			<div className={clsx('text-kgrey-menuText', propClassName)}>{prop}:</div>
			<div className={clsx('font-medium text-kdark dark:text-white', valueClassName)}>{value}</div>
		</div>
	);
}
