import React from 'react';
import { BillSubscriptionName } from './BillSubscriptionName';
import { BillSubscriptionDescr } from './BillSubscriptionDescr';
import clsx from 'clsx';
import { usePaymentContext } from '../context/PaymentContext';
import { roundNumber } from 'shared/services';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

type Props = {
	price: number;
	title: string;
	description: React.ReactNode;
	featuresList: React.ReactNode;
	isMuted?: boolean;
	isCustom?: boolean;
	range?: React.ReactNode;
	total?: number;
};

const MONTH_COUNT_IN_YEAR = 12;

export function PaymentTariff({
	price,
	title,
	description,
	featuresList,
	isMuted = false,
	isCustom = false,
	range = null,
	total = 0
}: Props) {
	const { t } = useTranslation();
	const { isRTL } = useTranslationHelpers();
	const { paymentFrequency } = usePaymentContext();
	const isYear = paymentFrequency === 'year';
	return (
		<div
			className={clsx(
				' p-tariff rounded-lg border-t-4 bg-white py-5 px-4 font-poppins shadow-p-tariff dark:bg-kdark-dark333 lg:w-[255px]',
				{
					'border-primary-dark': !isMuted,
					'border-kblue-3F598A': isMuted,
					'mr-4 ': !isRTL,
					'ml-4': isRTL
				}
			)}
		>
			<div className="p-tariff__price mb-3 align-bottom">
				<strong
					className={clsx('text-3xl font-semibold', {
						'text-primary-dark': !isMuted,
						'text-kdark-183B56': isMuted
					})}
				>
					${price}
				</strong>
				<span className="ml-1.5 text-xs text-kgrey-dark-4">/ {t('per_member_per_month')}</span>
			</div>
			<div
				className={clsx('p-tariff__info pb-3', {
					'border-b border-kgrey-light-grey-6': !isCustom
				})}
			>
				<BillSubscriptionName name={title} className={isMuted ? '!text-kdark-183B56' : ''} />
				<BillSubscriptionDescr description={description} />
			</div>
			{isCustom && (
				<div className="flex-center-end">
					<strong className="font-bold text-primary-dark">
						${roundNumber(isYear ? total * MONTH_COUNT_IN_YEAR : total)}
					</strong>
					<span className="ml-2 text-xs text-kgrey-dark-4">
						{isYear ? t('per_year') : t('per_month')}
					</span>
				</div>
			)}
			{!!isCustom && range}
			<div className="p-tariff__features pb-2.5 pt-4">{featuresList}</div>
		</div>
	);
}
