import React, { useEffect, useMemo, useState } from 'react';
import { PaymentTariff } from './PaymentTariff';
import Slider from '@material-ui/core/Slider';
import { usePaymentContext } from '../context/PaymentContext';
import { PlanType } from '../types';
import { parsePlans } from '../utils/parsePlans';
import { getPlanByMemberCount } from 'shared/services';
import { Trans, useTranslation } from 'react-i18next';
type Props = {
	planItems: PlanType[];
};

export function CustomTariff({ planItems }: Props) {
	const { t } = useTranslation();
	const [value, setValue] = useState<number>(1);
	const { paymentFrequency } = usePaymentContext();

	const [perMemberPrice, setPerMemberPrice] = useState<{ month: number; year: number }>({
		month: 0,
		year: 0
	});

	const parsedPlanItems = useMemo(() => parsePlans(planItems), [planItems]);

	const handleSliderChange = (event: any, newValue: number | number[]) => {
		const plan = getPlanByMemberCount(newValue as number, parsedPlanItems);

		setValue(+newValue);
		setPerMemberPrice(plan!.price);
	};

	useEffect(() => {
		if (parsedPlanItems) {
			const plan = getPlanByMemberCount(value, parsedPlanItems);

			if (!plan) return;

			setPerMemberPrice(plan!.price);
		}
	}, [parsedPlanItems]);

	return (
		<PaymentTariff
			// btnText="Contact Sales"
			description={
				<p>
					<Trans i18nKey="cost_for_users">
						Cost for <strong>{{ value }}</strong> users
					</Trans>
				</p>
			}
			featuresList={
				<>
					<p className="text-xs text-kgrey-menuText">{t('slide_dot_to_calculate')}</p>
				</>
			}
			price={perMemberPrice[paymentFrequency]}
			total={Math.round(value * perMemberPrice[paymentFrequency] * 100) / 100}
			title={t('your_rate')}
			isCustom
			range={
				<div>
					<Slider
						value={value}
						onChange={handleSliderChange}
						aria-labelledby="input-slider"
						min={1}
						className="!text-primary-dark"
					/>
				</div>
			}
		/>
	);
}
