import { ParsedPlanType, PlanType } from '../types';

export function parsePlans(planItems: PlanType[]) {
	const isCustomPlan = (amount: number) => amount > 50;

	return planItems
		.sort((a, b) => a.staffs_count_from - b.staffs_count_from)
		.reduce((acc: ParsedPlanType[], curr: PlanType) => {
			return [
				...acc,
				{
					id: curr.id,
					name: curr.title,
					members: `${curr.staffs_count_from}${isCustomPlan(curr.staffs_count_to) ? '' : '-'}${
						isCustomPlan(curr.staffs_count_to) ? '+' : curr.staffs_count_to
					}`,
					memberFrom: curr.staffs_count_from,
					memberTo: curr.staffs_count_to,
					price: {
						month: parseFloat(curr.monthly_price),
						year: parseFloat(curr.yearly_price)
					},
					yearlyDiscount: parseInt(curr.yearly_discount),
					type: curr.type
				}
			];
		}, []);
}
